.events-section {
    margin-top: 20px;
}



.events-date-link {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 26px;

    text-decoration-line: underline;

    background: none;
    border: none;
}

.evens-date-link, .active {
    background: #C7D2E9;
    border-radius: 8px;
    text-decoration-line: none;

    width: 160px;
    height: 58px;
}

.events-date-link:not(:first-child) {
    margin-left: 35px;
}

.date-links {
    margin-top: 40px;
}

.events-header {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 59px;

    margin: 0;
}

.cards-block {
    margin-top: 48px;

    display: grid;
    grid-template-columns: repeat(auto-fill, 312px);
    grid-auto-rows: auto;
    grid-gap: 1rem;
}

