.carousel-section {
	margin-top: 20px;
}

.carousel-img {
	width: 75%;
}

.carousel {
	display: flex;
	align-items: center;
}

.carousel-container {
	display: flex;
	justify-content: space-around;
}

.img-container {
	position: relative;
	order: 2;
}

.img-container img {
	width: 100%;
	height: auto;
}

.img-container .tickets-btn {
	position: absolute;
	bottom: 5%;
	left: 5%;

	background-color: #555;
	color: white;
	font-size: 16px;
	padding: 12px 24px;
	border: none;
	cursor: pointer;
	border-radius: 5px;

	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 22px;
	letter-spacing: 0em;
	text-align: left;
}

.arrow {
	border: none;
	background: none;
	height: fit-content;
}

.tickets-btn {
	position: relative;

	height: fit-content;
}

.left {
	order: 3;
}

.right {
	order: 1;
}

.carousel-bottom-link {
    background: #5D16D5;
	border-radius: 8px;
	padding: 8px;
    color: white;
	font-family: Roboto;
	font-size: 22px;
	font-style: normal;
	font-weight: 400;
	line-height: 26px;
	letter-spacing: 0em;
	text-align: left;
}
