.header {
	display: flex;
	justify-content: space-between;
	align-content: center;
}

.end-slot {
	display: flex;
	align-items: center;
}

.lang-toggle {
	display: flex;
	align-content: center;
	justify-content: center;
	align-items: center;

	background: #c7d2e9;
	border-radius: 4px;
	width: 64px;
	height: 52px;
	flex-shrink: 0;

	font-family: Roboto;
	font-style: normal;
	font-weight: normal;
	font-size: 22px;
	line-height: 26px;

	margin-right: 8px;
}

.search-input {
	appearance: none;
	width: 20vw;
	height: 48px;

	background: #f9f9fb;
	border: 1px solid #c7d2e9;
	box-sizing: border-box;
	border-radius: 26px;

	margin-right: 18px;
	background: white url(/public/search.svg) no-repeat;
	background-position: 95% 45%;
}

.login {
	border: none;
	background: none;
}

.modal-container {
	position: fixed; /* Stay in place */
	z-index: 1; /* Sit on top */
	left: 0;
	top: 0;
	width: 100%; /* Full width */
	height: 100%; /* Full height */
	overflow: auto; /* Enable scroll if needed */
	background-color: rgb(0, 0, 0); /* Fallback color */
	background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.modal-content {
	background-color: #fefefe;
	margin: 15% auto; /* 15% from the top and centered */
	padding: 20px;
	border: 1px solid #888;
	width: 30%; /* Could be more or less, depending on screen size */
}

.modal-header {
	display: inline-flex;
	width: 100%;
	justify-content: space-between;
}

.modal-h2 {
	font-family: Roboto;
	font-size: 50px;
	font-style: normal;
	font-weight: 400;
	line-height: 59px;
	letter-spacing: 0em;
	text-align: left;
	margin: 0;
}

.modal-close-btn {
	border: none;
	background: none;
	flex-grow: 0;
	width: 30px;
	height: 30px;
	padding: 0;
}

.modal-close-btn:hover {
	background: rgba(0, 0, 0, 0.1);
}

.login-input {
	background: #f9f9fb;
	border: 1px solid #c7d2e9;

	border-radius: 8px;
	height: 5vh;
	margin-top: 16px;
}

.login-form {
	display: flex;
	flex-direction: column;
}

.login-submit {
	background: #c7d2e9;
	border-radius: 8px;
	border: none;
	margin-top: 16px;
	height: fit-content;
	padding: 5px;

	font-family: Roboto;
	font-size: 30px;
	font-style: normal;
	font-weight: 400;
	line-height: 35px;
	letter-spacing: 0em;

}
