.footer {
	margin-top: 1rem;
	padding: 1rem;
	background: #f1f1f1;
	bottom: 0;
	left: 0;

	display: flex;
	justify-content: space-between;
	padding: 2% 10% 2% 10%;
}

.start-slot-links {
	display: flex;
	flex-direction: column;
}

.start-slot-links > a:not(:first-child) {
	margin-top: 16px;
}

.start-slot-link {
	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
}

.social-networks {
	margin-top: 40px;
}

.social-networks-header {
	font-family: Roboto;
	font-size: 24px;
	font-style: normal;
	font-weight: 400;
	line-height: 28px;
	letter-spacing: 0em;
	margin: 0;
}

.social-networks-links {
	margin-top: 16px;
}

.social-networks-links > a:not(:first-child) {
	margin-left: 25px;
}

.subscribe-form {
	margin-top: 35px;
}

.email {
	border: none;
	background: none;
	border-bottom: 1px solid black;
	padding-bottom: 12px;

	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;
}

.sub-button {
	background: #5D16D5;
	border-radius: 8px;
	color: white;
	border: none;
	padding: 12px 20px 12px 20px;
	margin-left: 16px;
}

.personal-data-label {
	font-family: Roboto;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 19px;
	letter-spacing: 0em;
	text-align: left;
	margin-left: 8px;
}

.personal-data-checkbox {
	width: 24px;
	height: 24px;
}

.personal-agree {
	margin-top: 12px;
	display: flex;
	align-items: center;
}

::-webkit-input-placeholder {
	/* WebKit, Blink, Edge */
	color: #000000;
	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;
}
:-moz-placeholder {
	/* Mozilla Firefox 4 to 18 */
	color: #000000;
	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;

	opacity: 1;
}
::-moz-placeholder {
	/* Mozilla Firefox 19+ */
	color: #000000;
	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;

	opacity: 1;
}
:-ms-input-placeholder {
	/* Internet Explorer 10-11 */
	color: #000000;
	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;
}
::-ms-input-placeholder {
	/* Microsoft Edge */
	color: #000000;
	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;
}

::placeholder {
	/* Most modern browsers support this now. */
	color: #000000;
	font-family: Roboto;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: 21px;
	letter-spacing: 0em;
	text-align: left;
}
